@import url('https://fonts.googleapis.com/css2?family=Bacasime+Antique&family=Noto+Sans:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@100;300;400;500;700;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .headerHover{
    @apply px-2 h-[80%] flex items-center border border-transparent hover:border-white cursor-pointer duration-100
  }
  .footerLink {
    @apply text-sm font-titleFont tracking-wide hover:text-gray-100 cursor-pointer hover:underline duration-150;
  }
  .productLi{
    @apply text-gray-600 hover:text-black text-sm font-medium border-b-[1px] border-b-gray-400 flex items-center justify-end
    gap-2 hover:cursor-pointer hover:border-b-gray-700 duration-300 w-full
  }
}